.navBar{
    display: flex;
    width: 40%;
    justify-content: space-evenly;
}
.navBar button{
    outline: none;
    border: none;
    background-color: transparent;
    font-size: 1.3em;
    color: white;
    align-self: flex-end;
}
.navBar button:focus{
    border: 1px solid grey;
}